import * as ExpiredStorage from 'expired-storage';

export class StorageClient {

  expiredStorage = null;

  get instance() {
		return StorageClient.instance;
	}

	set instance(value) {
		StorageClient.instance = value;
  }

  constructor() {
    this.initializeStorage();
    if (!StorageClient.instance) { 
      StorageClient.instance = this;
    }
  }

  initializeStorage() {
    if (typeof localStorage === "undefined" || localStorage === null) {
      var LocalStorage = require('node-localstorage').LocalStorage;
      localStorage = new LocalStorage('./scratch');
    }
    this.expiredStorage = new ExpiredStorage.default();
  }

  getItem(key) {
    return localStorage.getItem(key);
  }

  setItem(key, value) {

  }

  getJson(key) {

  }

  setJson(key, value) {

  }

}
